import React from 'react';
import { BrowserRouter, Route, Switch, withRouter} from 'react-router-dom';
import HeaderComponent from '../components/HeaderComponent';
import HomePage from '../components/HomePage';
import LoginPage from '../components/LoginPage';
import RegisterPage from '../components/RegisterPage';
import NewsDetailPage from '../components/NewsDetailPage';
import { TransitionGroup, Transition } from 'react-transition-group';

const timeout = { enter: 300, exit: 200 }

const Content = withRouter(props => (
    <div>
        <HeaderComponent />
            <TransitionGroup
                childFactory={child => React.cloneElement(
                    child,
                    {
                        classNames:"fade",
                        timeout: {timeout}
                    }
                )}
            >
                <Transition
                    key={props.location.key}
                    timeout={timeout}
                    appear
                >
                    {
                        ( status ) => {
                            return (
                                <div>
                                    <Switch location={props.location}>
                                        <Route path="/" render={(routeProps) => (<HomePage {...routeProps} status={status} />)} exact={true} />
                                        <Route path="/login" render={(routeProps) => (<LoginPage {...routeProps} status={status} />)} exact={true}  />
                                        <Route path="/register" render={(routeProps) => (<RegisterPage {...routeProps} status={status} />)} exact={true} />
                                        <Route path="/newsdetail/:news_id" render={(routeProps) => (<NewsDetailPage {...routeProps} status={status} />)} exact={true} />
                                    </Switch>
                                </div>
                            )
                        }
                    }
                </Transition>
            </TransitionGroup>
    </div>
    )

);

class AppRouter extends React.Component {
    constructor(props) {
        super(props);
        // console.log("AppRouter");
        // console.log(props);
    }
   
    render() {
        return (
            <BrowserRouter>
                <Content />
            </BrowserRouter>
        );
    }
}

export default AppRouter;



